<template>
  <div class="container chatContent">
    <div id="chat">
      <div class="chatBox">
        <div class="chatBox-top">
          <div class="chatBox-top-left">
            <van-icon name="arrow-left" @click="back" />
            <van-image
              round
              width="32"
              height="32"
              :src="avatar"
              class="avatar"
            />
            <!-- <img src="../../assets/user/avatar.svg" alt="" class="avatar" /> -->
            <span>{{ form.name }}</span>
          </div>
          <div class="chatBox-top-right">
            <van-icon name="phone-o" />
            <van-icon name="ellipsis" />
          </div>
        </div>
        <div class="chatBox-middle">
          <div class="chatInfo" id="chatInfo">
            <div
              class="chatUser-box"
              v-for="(item, index) in infoList"
              :key="index"
              :class="[current == item.id ? 'chatUser-box1' : 'chatUser-box']"
            >
              <div class="chatUser-box-img">
                <van-image
                  round
                  width="2.5rem"
                  height="2.5rem"
                  :src="item.images"
                />
              </div>
              <div class="chatUser-info">
                <div
                  class="chatUser-info-text"
                  :class="[
                    current == item.id
                      ? 'chatUser-info-text1'
                      : 'chatUser-info-text',
                  ]"
                >
                  <span>{{ item.message }}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="inputChat">
          <van-field
            v-model="messages"
            placeholder="说点什么吧..."
            @blur="getSends"
          >
            <template #extra>
              <van-icon name="smile-o" />
              <van-icon name="volume-o" />
            </template>
          </van-field>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import avatar from "../../assets/user/avatar.svg";
import chatAvatar from "../../assets/user/chatAvatar.svg";
import { Toast } from "vant";
export default {
  data() {
    return {
      form: {
        name: "苟富贵",
      },
      messages: "",
      nowDate: "", // 当前日期
      current: 2,
      avatar,
      infoList: [
        {
          id: 2,
          name: "苟富贵",
          time: "2021-4-23 18:00:05 星期五",
          images: chatAvatar,
          message: "您好！",
        },
        {
          id: 1,
          name: "廖东",
          time: "2021-4-23 17:59:50 星期五",
          images: avatar,
          message: "您好！",
        },
      ],
    };
  },
  mounted() {
    // this.form = this.$route.params;
    this.scrollToBottom();
  },
  updated() {
    this.scrollToBottom();
  },
  methods: {
    getSend() {
      if (this.messages == "") {
        Toast("请输入内容");
        return;
      }
      let userData = {
        id: 1,
        name: "廖东",
        time: this.nowDate,
        images: avatar,
        message: this.messages,
      };
      this.infoList.push(userData);
      this.messages = "";
    },
    getSends() {
      if (this.messages == "") {
        Toast("请输入内容");
        return;
      }
      let customerData = {
        id: 2,
        name: "苟富贵",
        time: this.nowDate,
        images: chatAvatar,
        message: this.messages,
      };
      this.infoList.push(customerData);
      this.messages = "";
    },
    scrollToBottom() {
      this.$nextTick(() => {
        let container = this.$el.querySelector("#chatInfo");
        container.scrollTop = container.scrollHeight;
      });
    },
    back() {
      this.$router.back();
    },
  },
};
</script>

<style scoped>
.container {
  padding: 0;
  height: 100vh;
}
#chat .chatBox {
  /* width: 100vw; */
  height: 100vh;
  background-color: #f9f9f9;
  overflow: auto;
  border-radius: 0.625rem;
}

.avatar {
  margin: 0 5px;
  width: 32px;
  height: 32px;
  border-radius: 50%;
}
#chat .chatBox-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  padding: 10px;
}

#chat .chatBox-top-left {
  display: flex;
  align-items: center;
}

#chat .chatBox-top-right >>> .van-icon {
  font-size: 24px;
}
#chat .chatBox-top-right >>> .van-icon:last-child {
  transform: rotate(90deg);
  margin-left: 15px;
}

#chat .chatBox-middle {
  width: 100%;
  height: calc(100vh - 110px);
  overflow: auto;
  /* background-color: #f9f9f9; */
}

#chat .chatInfo {
  width: 90%;
  height: 94%;
  margin: 1.25rem auto;
  overflow: auto;
}
#chat .chatInfo::-webkit-scrollbar {
  display: none;
}

#chat .chatUser-box {
  width: 100%;
  margin-bottom: 6px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

#chat .chatUser-box-img {
  display: flex;
}

#chat .chatUser-info {
  margin: 0 0.5rem;
}

#chat .chatUser-info-name {
  font-size: 0.875rem;
  color: #888;
  display: flex;
  flex-direction: row;
}

#chat .nowDate {
  margin: 0 0.625rem;
}

#chat .chatUser-info-text {
  margin-top: 0.3125rem;
  max-width: 20rem;
  padding: 0.5rem;
  /* background-color: #e8e8e8; */
  background-color: #fff;
  border-radius: 0.5rem;
  float: left;
  table-layout: fixed;
  word-break: break-all;
  overflow: hidden;
}

#chat .chatUser-info-text span {
  font-size: 0.9375rem;
  line-height: 1.5625rem;
}
#chat .chatUser-box1 {
  flex-direction: row-reverse;
}
#chat .chatUser-info-name1 {
  flex-direction: row-reverse;
}
#chat .chatUser-info-text1 {
  float: right;
  background: #ecefff;
}
.inputChat {
  position: absolute;
  bottom: 20px;
}
.inputChat >>> .van-field {
  width: 90vw;
  height: 40px;
  margin-left: 5vw;
  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.04);
  border-radius: 7px;
}
.inputChat >>> .van-icon {
  font-size: 22px;
}
.inputChat >>> .van-icon:last-child {
  margin-left: 8px;
}
</style>